export default {
	data() {
		return {
			labelList:[
				{name:'全部',number:25,type:'all'},
				{name:'好评',number:23,type:'good'},
				{name:'中评',number:1,type:'secondary'},
				{name:'差评',number:1,type:'poor'},
				{name:'有图',number:12,type:'img'},
				{name:'视频',number:2,type:'video'},
				{name:'追加',number:2,type:'append'}
			],
			labelIndex:0,
			ratingsList:[
				
			],
			videoDirection:0,
			showFullscreenBtn:true,
			showPlayBtn:true,
			isPlayVideo:true,
			videoSrc:''

		};
	},
	onReady: function (res) {
		this.videoContext = uni.createVideoContext('myVideo')
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		uni.showToast({ title: '触发上拉加载' });
	},
	methods: {
		loadRatings(index){
			this.labelIndex = index;
			uni.showToast({
				title:"切换评论列表"
			})
			//实际应用中，请求对应类型的评论列表，覆盖this.ratingsList
			/*
			let type = this.labelList[index].type; // 评论类型
			......
			*/
		},
		playVideo(path) {
			this.videoSrc = path;
			// this.isPlayVideo = true;
			this.$nextTick(function() {
				this.videoContext.requestFullScreen({direction:0});
			});
			
		},
		stopPlayVideo(){
			this.videoContext.pause();
		},
		videoPause(){
			// this.isPlayVideo = false;
			this.videoSrc = '';
		}, 
		viderFullscreen(e){
			if(e.detail.fullScreen){
				this.videoContext.play();
			}else{
				this.stopPlayVideo();
			}
		},
		showBigImg(src,srclist){
			uni.previewImage({
				current:src,
				urls: srclist
			});
		}
	},
}
